import { Component, OnInit } from '@angular/core';
import {Router,ActivatedRoute} from '@angular/router';
import { ServiceService } from '../service.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
profile = [];
newProfile = {
  jobId:'',
resourceName:'',
resourceEmail:'',
resourceNumber:'',
resourceTechnology:'',
resourceSpec:'',
resourceExp:'',
resourceVisaType:'',
resourceLocation:'',
resourceBillRate:'',
resourceAvailability:true,
client:'',
employerName:'',
employerEmail:'',
employerNumber:'',
comments:''
}
fileData:File;
selectedFiles: FileList;
currentFileUpload: File;
tech = ['node','angular','java','spring'];
urlVal:any;
formEditable:boolean;
userFilter: any = '';
fileArrList:any = [];
 curDate: number = Date.now();
 jobdata;
 userMobile;
 role;
  constructor(private service: ServiceService, private router: Router,  private routeActive: ActivatedRoute,private cookieService: CookieService) { }

  ngOnInit() {
   this.urlVal = this.routeActive.snapshot.url;
   //let jobdata = this.routeActive.snapshot.queryParams;
    this.userMobile = this.cookieService.get('mobile');
    this.role = this.cookieService.get('role');
   this.service.jobData.subscribe(job => this.jobdata = job)

    if (this.urlVal == "profiles") {
      this.formEditable = false;
    }else{
      this.formEditable = true;
    }
    this.getProfileData();
  }

  addRow = function(){
    this.profile.push({});
  };
  deleteRow(index){
    this.profile.splice(index,1);
  };

   selectFile(event) {
   alert('innnnn')
    this.selectedFiles = event.target.files;
  }
  saveProfile(obj){
    console.log(obj);
    let arrData = [obj];
    let userMobile = this.cookieService.get('mobile');
    let  data = JSON.stringify(obj);
    if(this.selectedFiles != null && this.selectedFiles.length>0){
     this.currentFileUpload = this.selectedFiles[0];
    }
     
  this.service.saveProfile(data,userMobile,this.currentFileUpload).subscribe(
    data => {
      alert('success');
      this.getProfileData();
    },
    error =>{
      alert('error')
    }
  );
  };

  downloadProfile(mobile){
    window.location.href = 'http://localhost:8080/api/downloadProfile/'+mobile;
  }

showHistory(value,action){
 console.log('value::'+value);
  this.router.navigate(['profile-history', value,action]);
}
getProfileData(){
this.service.getProfile(this.userMobile,this.role).subscribe(
    data => {
      let obj:any = data;
      if (obj != null && obj.length>0 ) {
          this.profile = obj;
           console.log(this.profile);
          if(this.jobdata.jobId != null){
            this.profile.push(this.jobdata);
            this.service.changeJob({});
          }
      }else{
        if(this.jobdata.jobId != null){
          this.profile.push(this.jobdata);
          this.service.changeJob({});
        }else{
        this.profile = [];
        alert('No  Data Found.')
      }
      }
    },
    error => {
      alert('Could Not Connect To Server! Try Again.')
    }
  );
}

}



